import { serializeStyles } from '@emotion/serialize';
import { cssStringToBuilderReturnType } from '../commonUtils';
import { BuilderReturnType } from '../../types';
import { BuildStackStylesProps } from '../../types/stackTypes';

export const buildStackStyles = ({
  selectors: { itemSelector, parentSelector },
}: // horizontalGap = 0,
BuildStackStylesProps): BuilderReturnType => {
  const parentCss = serializeStyles([{
    [parentSelector]: {
      width: '100%',
      height: '100%',
    },
  }]).styles;

  const itemCss = serializeStyles(
    [{
      [`${itemSelector}:nth-child(n)`]: {
        width: '100%',
        height: '100%',
        position: 'absolute',
        top: '0',
        left: '0',
        opacity: '0',
        transition: 'opacity 1s',
      },
    },
    {
      [`${itemSelector}.active`]: {
        opacity: '1',
      },
    },
  ]).styles;

  return cssStringToBuilderReturnType(parentCss + itemCss);
};
